@import "shared";

$interactive-content-leading-padding: 12px;
$interactive-content-trailing-padding: 12px;
$interactive-content-top-padding: 4px;
$interactive-content-bottom-padding: 8px;
$video-controls-height: 40px;

@keyframes fadeIn {
	from { opacity: 0; }
	to { opacity: 1; }
}

@keyframes fadeOut {
	from { opacity: 1; }
	to { opacity: 0; }
}

@keyframes expand {
	from { aspect-ratio: 99999; }
	to { aspect-ratio: 1.5; }
}

@keyframes collapse {
	from { aspect-ratio: 1.5; }
	to { aspect-ratio: 99999; }
}

#interactiveContent {
	display: block;
	position: relative;
	z-index: 1;
	max-width: 750px;
	color: white;
	background-color: black;
}

.interactiveContentFullSize {
	max-width: 750px;
	aspect-ratio: 1.5;
}

.interactiveContentExpanded {
	aspect-ratio: 1.5;
	animation: expand ease-in-out 1s;
}

.interactiveContentCollapsed {
	aspect-ratio: 99999;
	animation: collapse ease-in-out 1s;
}

.interactiveContentCollapsedInitial {
	aspect-ratio: 99999;
}

#interactiveContentBackground {
	overflow: hidden;
	z-index: 2;
	background-image: url(/crossing_preview/images/backgroundNew.jpg);
	background-size: cover;
}

#interactiveContentBackgroundImage {
	width: 100%;
	height: 100%;
}

#interactiveContentBackgroundOverlayImage {
	width: 100%;
	height: 100%;
}

#interactiveContentImage {
	min-width: 200%;
    min-height: 125%;
    position: relative;
    cursor: grab;
}

#interactiveContentImage:active {
    cursor: grabbing;
}

#interactiveContentImageNonInteractive {
	width: 100%;
    height: 100%;
    object-fit: contain;
    background-color: black;
}

#interactiveContentMenuContainer {
	display: block;
	position: absolute;
	bottom: 12px;
	left: 12px;
	width: 284px;
	background-color: rgba(60, 42, 16, 0.6);
	padding: 4px 8px 8px 8px;
	z-index: 4;
	box-shadow: 6px 6px 2px #00000055;
}

.interactiveContentMenuContainerInitial {
	opacity: 0;
	pointer-events: none;
}

.interactiveContentMenuContainerHidden {
	opacity: 0;
	pointer-events: none;
	animation: fadeOut ease-in-out 1s;
}

.interactiveContentMenuContainerShown {
	opacity: 1;
	animation: fadeIn ease-in-out 1s;
}

@media only screen and (max-width: $one-column-breakpoint) {
	#interactiveContentMenuContainer {
		position: relative;
		bottom: unset;
		left: unset;
		margin-right: auto;
		width: unset;
		box-shadow: unset;
	}

	#interactiveContentMenuContainerBackground {
		background: $article-background-color;
		padding: 8px;
	}

	.interactiveContentMenuContainerInitial {
		opacity: 1;
		pointer-events: unset;
	}

	.interactiveContentMenuContainerHidden {
		opacity: 1;
		pointer-events: unset;
		animation: unset;
	}

	.interactiveContentMenuContainerShown {
		opacity: 1;
		animation: unset;
	}
}

#interactiveContentMenu {
	display: block;
	color: black;
	background-color: #584930C0;
	text-align: left;
}

#interactiveContentMenuTitle {
	color: $interactive-content-title;
	/* TODO: Update to Rocky. */
	font-family: Arial;
	font-size: 18pt;
	font-weight: bold;
	padding-left: $interactive-content-leading-padding;
	padding-right: $interactive-content-trailing-padding;
	padding-top: $interactive-content-top-padding;
}

#interactiveContentMenuChapter {
	/* TODO: Update to Rocky. */
	font-family: Arial;
	font-size: 12pt;
	font-weight: bold;
	color: $interactive-content-chapter-number;
	background-color: $interactive-chapter-background;
	padding-left: $interactive-content-leading-padding;
	padding-right: $interactive-content-trailing-padding;
	padding-top: 2px;
}

#interactiveContentMenuSubtitle {
	font-family: Arial;
	font-size: 10pt;
	color: $interactive-content-subtitle;
	padding-left: $interactive-content-leading-padding;
	padding-right: $interactive-content-trailing-padding;
	padding-top: 8px;
	padding-bottom: 8px;
}

#interactiveContentMenuButtonContainer {
	display: flex;
	padding-left: $interactive-content-leading-padding;
	padding-right: $interactive-content-trailing-padding;
	padding-bottom: $interactive-content-bottom-padding;
	flex-wrap: wrap;
	row-gap: 4px;
}

.interactiveContentMenuButton {
	font-family: Arial;
	font-size: 9pt;
	font-weight: bold;
	text-align: left;
	color: white;
	cursor: pointer;
	flex-grow: 1;
	flex-basis: 30%;
	white-space: nowrap;
}

.interactiveContentMenuButtonDisabled {
	color: gray;
}

.interactiveContentMenuButtonText {
	padding-left: 0px;
}

.interactiveContentMenuButtonImage {
	width: 9px;
}

.invisible {
	opacity: 0;
}

.hidden {
	display: none !important;
}

.backgroundFadeIn {
	animation: fadeIn ease-in-out 1s;
	opacity: 1;
}

.backgroundFadeOut {
	animation: fadeOut ease-in-out 1s;
	opacity: 0;
}

.captionsFadeIn {
	animation: fadeIn ease-in-out 0.25s;
	opacity: 1;
}

.captionsFadeOut {
	animation: fadeOut ease-in-out 0.25s;
	opacity: 0;
}

/* Video player */

