.videoPlayerPlaceholderTextHeader {
    color: $bold-title;
    margin: 0 0 4px 0;
    font-size: 2.5em;
    font-family: impact;
    text-shadow: 4px 4px 4px #00000044;
}

#videoPlayer {
	width: 100%;
	height: 100%;
}

#videoPlayerControls {
	position: absolute;
	width: 100%;
	height: $video-controls-height;
	bottom: 0;
	display: flex;
	align-items: center;
	color: $media-controls;
}

#videoPlayerControls > * {
	display: inline;
	margin-left: 4px;
	margin-right: 4px;
}

#videoPlayerControls a {
	cursor: pointer;
}

#playbackBar {
	background: linear-gradient(rgb(116,88,71), rgb(21,17,13));
	flex-grow: 1;
	height: 10px;
}

#playbackSelector {
	background: blue;
	width: 20px;
	height: 100%;
	position: relative;
	float: left;
}

/* Photo player */

#interactivePhotoPlayer {
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 3;
	background: black;
}

#photoPlayerContainer {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: $video-controls-height;
}

#photoPlayer {
	width: 100%;
	height: 100%;
}

#photoPlayerControls {
	position: absolute;
	width: 100%;
	height: $video-controls-height;
	bottom: 0;
	display: flex;
	align-items: center;
	color: $media-controls;
	font-family: impact;
    text-transform: uppercase;
    font-size: 1.25em;
}

#photoPlayerControls > * {
	display: inline;
	margin-left: 4px;
	margin-right: 4px;
}

#photoPlayerControls a {
	cursor: pointer;
}

#photoPlayerControls a:hover {
	color: #fff;
	text-decoration: none;
}

#photoSlideCaption {
	width: 100%;
	bottom: 0;
	left: 0;
	position: absolute;
	min-height: 100px;
	max-height: 100%;
	background: red;
	padding: 12px;
	box-sizing: border-box;
}

.photoSlideCaptionText {
	width: 100%;
	max-height: 100%;
	text-align: left;
	display: inline-block;
}

.photoSlide {
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	position: absolute;
	object-fit: contain;
	opacity: 0;
	transition: opacity 0.5s linear;
}

.photoSlideShown {
	opacity: 1;
}
